@charset "UTF-8";
@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?14015051');
  src: url('../font/fontello.eot?14015051#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?14015051') format('woff2'),
       url('../font/fontello.woff?14015051') format('woff'),
       url('../font/fontello.ttf?14015051') format('truetype'),
       url('../font/fontello.svg?14015051#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
