@media (max-width: 767px) {
  .clients__slider-container {
    margin-top: 30px;
  }
}
.clients__item {
  padding: 30px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
@media (max-width: 992px) {
  .clients__item {
    padding: 22px;
  }
}
@media (max-width: 767px) {
  .clients__item {
    display: block;
  }
}
.clients__item:after, .clients__item::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.5s;
  pointer-events: none;
}
.clients__item::before {
  background-image: linear-gradient(to right, #fff 0%, rgba(255, 255, 255, 0.5) 101%);
}
.clients__item:after {
  background-image: linear-gradient(to left, #fff 0%, rgba(255, 255, 255, 0.5) 0%);
}
.clients__item.swiper-slide-prev::before {
  opacity: 1;
}
.clients__item.swiper-slide-next::after {
  opacity: 1;
}
.clients__media {
  width: 50%;
  position: relative;
}
@media (max-width: 767px) {
  .clients__media {
    width: 100%;
    height: 200px;
  }
}
.clients__play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 71px;
  height: 71px;
  background: #fff;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.7;
  cursor: pointer;
}
.clients__play-btn svg {
  width: 60px;
  height: 60px;
  opacity: 0.4;
  transition: all 0.3s;
}
.clients__play-btn:hover svg {
  opacity: 1;
}
.clients__info {
  width: calc(50% - 60px);
  padding: 54px 0;
}
@media (max-width: 992px) {
  .clients__info {
    padding: 30px 0;
    width: calc(50% - 30px);
  }
}
@media (max-width: 767px) {
  .clients__info {
    width: 100%;
    padding: 20px 0 0 0;
  }
}
.clients__content {
  font-family: Montserrat;
  font-size: 18px;
  font-style: italic;
  line-height: 1.5;
  color: #1f1f1f;
}
@media (max-width: 992px) {
  .clients__content {
    font-size: 16px;
  }
}
.clients__author {
  font-family: Montserrat;
  font-weight: 700;
  margin-top: 22px;
  line-height: 1.2;
}
@media (max-width: 767px) {
  .clients__author {
    margin-top: 12px;
  }
}
.clients__position {
  font-family: Montserrat;
  font-size: 14px;
  margin-top: 4px;
}
.clients__img {
  height: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  border-radius: 10px;
}

.global_video_player {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 13;
  transition: all 0.4s;
  pointer-events: none;
  opacity: 0;
}
.global_video_player.active {
  opacity: 1;
  pointer-events: all;
}
.global_video_player .close {
  position: absolute;
  top: 24px;
  right: 24px;
  cursor: pointer;
  width: 56px;
  height: 56px;
  transition: all 0.4s;
  z-index: 10;
}
.global_video_player .close:hover {
  transform: rotate(90deg);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(25.5px);
}

.video-box {
  position: relative;
  z-index: 2;
  max-width: 600px;
  width: 100%;
}
.video-box iframe,
.video-box video {
  max-width: 600px;
  width: 100%;
  height: auto;
  object-fit: contain;
}
.video-box iframe {
  height: 90vh;
}

.form {
  background-repeat: no-repeat;
  background-size: cover;
}
.form__container {
  max-width: 1210px;
}
.form__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 120px 0;
}
@media screen and (max-width: 992px) {
  .form__wrapper {
    flex-direction: column;
    padding: 60px 0;
  }
}
.form__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media screen and (max-width: 992px) {
  .form__content {
    margin-bottom: 15px;
  }
}
.form__title {
  font-family: Italiana;
  font-size: 70px;
  letter-spacing: 2.8px;
  line-height: normal;
}
@media screen and (max-width: 992px) {
  .form__title {
    font-size: 60px;
  }
}
.form__info {
  font-size: 20px;
}
.form__mail {
  display: block;
  width: fit-content;
  color: #69ae96;
  margin-bottom: 13px;
  position: relative;
}
.form__mail::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background: #69ae96;
  bottom: 5px;
  left: 0;
}
.form__adress {
  margin-bottom: -8px;
}
.form__fields {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 570px;
  width: 100%;
}
@media screen and (max-width: 1200px) {
  .form__fields {
    max-width: 500px;
  }
}
.form__field {
  border: 1px #c9ccd4 solid;
  border-radius: 6px;
  background: #fff;
  color: #c9ccd4;
  font-size: 16px;
  font-weight: 300;
  height: 60px;
  padding: 0 22px;
  font-family: inherit;
  transition: all 0.3s;
}
.form__field::placeholder {
  color: #000;
  opacity: 0.5;
}
.form__field:hover {
  border-color: #3b7a64;
}
.form__field:focus {
  border-color: #3b7a64;
  box-shadow: 0px 0px 13px 1px rgb(105, 174, 150);
}
.form__textarea {
  font-size: 16px;
  padding-top: 17px;
  min-height: 132px;
  margin-bottom: 16px;
}
.form__button {
  box-shadow: 0 12px 24px 0 rgba(44, 39, 56, 0.08);
  font-family: inherit;
  line-height: normal;
  font-size: 16px;
  cursor: pointer;
}
.form__button img {
  width: 21px;
  margin-right: 8px;
}
.form__loader {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  display: none;
}
.form__loader.active {
  display: block;
}
.form__success, .form__fail {
  margin-bottom: 10px;
  font-size: 20px;
  color: #69ae96;
  transition: all 0.3s;
  display: none;
}
.form__success.active, .form__fail.active {
  display: block;
}

.error {
  border: 1px red solid;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}